
.skill_item {
    border: 1px solid red;
    box-sizing: border-box;
    background-color: #937575;
    min-width: 270px;
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 15px;
    padding: 8px 0;
    .icon {
        width: 60px;
        height: 60px;
        background-color: white;
    }
    .description {
        width: 95%;
    }
}



